export const devMode = {
  data() {
    return {
      Ho_userID: '0977024351',
      Ho_pwd: 'qweasd123',
      autoLogin: true,
    }
  },
  methods: {
    HoHoLoginProcess() {
      return this.$axios('http://o2oapi.test.com.tw/api/Login/AnonymousLogin', {
        method: 'POST',
        body: { userid: 'HOHOAdmin', password: 'HOHOAdmin' }
      }).then(res => {
        return res.status === 1
          ? this.$axios('http://o2oapi.test.com.tw/api/Login/Login', {
            method: 'POST',
            body: {
              userid: this.Ho_userID,
              password: this.Ho_pwd,
              Ticket: res.Ticket,
              PlatformDetail: 'Desktop,Windows 10,Chrome',
              platform: '1'
            }}).then(res => res.Ticket)
          : Promise.reject();
      })
    },
    async _devLogin() {
      const { HoHoLoginProcess, initProcess, Ho_userID, token } = this;
      
      if(!token) await HoHoLoginProcess().then(Ticket => {
          this.userID = Ho_userID;
          this.tokenAPIObj = {
            userID: Ho_userID,
            method: 'POST',
            url: 'http://o2oapi.test.com.tw/api/Login/GetMessageToken',
            data: { userID: Ho_userID, Ticket }
          };
        });
      initProcess();
    },
  },
  created() {
    if(this.isDev && this.autoLogin) this._devLogin();
  }
}