import errorMsg from '../assets/errorMsg.json'
import axios from "axios";

const BASE_URL = 'http://192.168.100.242:5566';
const BASE_API_URL = 'http://192.168.100.242:5566/api/';
const axiosInstance = axios.create({ baseURL: BASE_API_URL, timeout: 30000 });
let MyPlugin = {};

axiosInstance.interceptors.request.use(
  config => {
    const DATA = config.data
    if(DATA) {
      const { headers, body } = DATA;
      if(headers) config.headers = headers;
      if(body) config.data = body;
    }
    return config
  },
  err => Promise.reject(err)
);
axiosInstance.interceptors.response.use(
  res => res.data,
  err => err
);

MyPlugin.install = function (Vue) {
  Vue.prototype.$BASE_URL = BASE_URL;
  Vue.prototype.$formatTime = (date, format = 'YYYY/MM/DD h:mm') => {
    let dateString = "",
      YY, MM, DD, h, mm;

    if(!date) return '';
    if(Date.parse(date)) {
      const datetime = new Date(date);
      YY = datetime.getFullYear();
      MM = datetime.getMonth() + 1;
      DD = datetime.getDate();
      h = datetime.getHours();
      mm = datetime.getMinutes();
      MM = MM < 10 ? `0${MM}` : MM
      DD = DD < 10 ? `0${DD}` : DD
      h = h < 10 ? `0${h}` : h;
      mm = mm < 10 ? `0${mm}` : mm;
    } else {
      const splitDate = date.split(' ');
      const DateArr = splitDate[0].split('-');
      const TimeArr = splitDate[1].split(':');
      YY = DateArr[0];
      MM = DateArr[1];
      DD = DateArr[2];
      h = TimeArr[0];
      mm = TimeArr[1];
    }
    switch (format) {
      case 'YYYY/MM/DD h:mm':
        dateString = `${YY}/${MM}/${DD} ${h}:${mm}`;
        break;
      case 'YYYY/MM/DD':
        dateString = `${YY}/${MM}/${DD}`;
        break;
      case 'MM/DD':
        dateString = `${MM}/${DD}`;
        break;
      case 'h:mm':
        dateString = `${h}:${mm}`;
        break;
      default:
        break;
    }
    return dateString;
  },
  Vue.prototype.$_errorHandler = (STATUS, text) => { // _
    console.error({
      status: STATUS,
      errorMsg: text ? errorMsg[STATUS] + ` ${text}` : errorMsg[STATUS]
    });
    window.dispatchEvent(new CustomEvent('onError', {
      detail: {
        status: STATUS,
        errorMsg: errorMsg[STATUS]
      }
    }))
    return text ? errorMsg[STATUS] + ` ${text}` : errorMsg[STATUS];
  }
  Vue.prototype.$axios = (url , config) => {
    switch (config.method.toLowerCase()) {
      case "post":
        return axiosInstance.post(url, config);
      case "get":
        return axiosInstance.get(url, config);
      default:
        return false;
    }
  },
  Vue.prototype.$debounce = function(fn, timer, delay = 450) {
    return ((...args) => {
      clearTimeout(timer)
      timer = setTimeout(() => {
        timer = null
        fn(...args)
      }, delay)
    })()
  }
}

export default MyPlugin