<template lang="pug">
.outside(:class="[{'dev': isDev}, {'isOpen': isOpen}]")
  template(v-if="isDev")
    b-button(@click="_devLogin") 
    b-button(@click="closeConnect") WS
    b-button(@click="toggleModal") 
    b-button(@click="addNewConnect") 

  .wrapper(:class="[{'active': isOpen}, {'unLogin': !isLogin}, {'viewList' : mobileViewList && isLogin}, {'fullScreen' : isFullScreen}]")
    .chatList
      .chatList_header
        .px-2
          b-icon-chevron-left.mr-2.d-lg-none(@click="hideModal(true)")
        SearchInput(v-model="filterInput")
        Dropdown(v-model="filterSelect")
      .chatList_body(:class="{'empty': !filterChatList.length}")
        ListItem(
          v-for="item in filterChatList"
          :id="item.conversationID"
          :key="item.conversationID"
          :item="item"
          :userID="userID"
          @connect="connect"
        )
        //- 20230925 
        //- b-spinner.d-block.mx-auto(v-if="!chatListLoadingFinish" v-b-visible="chatListLazyLoad" label="Loading..." variant="primary")
        .emptyListItem(v-if="!filterChatList.length")
          h2 
          span 
      HoCS(:userID="userID" @connect="connect")
    .container
      Header(
        :targetPerson="targetPerson"
        :isFullScreen="isFullScreen"
        @close="hideModal(true)"
        @back="hideModal(false)"
      )
      .body(ref="msgWrapper" @scroll="scrollEvent($event)")
        .welcomeWord(v-if="!targetPerson.conversationID")
          template(v-if="isLogin")
            p HoHo
            //- b-button.d-block.mx-auto(v-if="isMobile" @click="mobileViewList = true") 
          template(v-else-if="!manualCloseConnect && reConnectCount < 6")
            p ...
            span.d-none {{ reConnectCount }}
            b-spinner.d-block.mx-auto(label="Loading..." variant="primary")
          template(v-else)
            p 
            b-button.d-block.mx-auto(@click="locationReload") 
        .innerBody(v-else ref="msgBody")
          template(v-if="!msgLoadingFinish")
            b-spinner.d-block.mx-auto(v-b-visible="msgLazyLoad" label="Loading..." variant="primary")

          template(v-for="(item, i) in messageList")
            MessageItem(
              :key="i"
              :item="item"
              :userID="userID"
              :class="{'mb-3': i !== messageList.length}"
            )
      .footer(v-if="targetPerson.conversationID")
        MessageNotify(ref="useMsgNotify" :leaveEnd="leaveEnd" @handleClick="goEnd")
        UploadFile(@finish="sendFile")
        Textarea(ref="textArea" v-model='input' @handleEnter="sendMsg")
        b-icon-symmetry-horizontal.send(@click="sendMsg") 
</template>

<script>
import Vue from 'vue'
import MyPlugin from './plugins/global.js'
import VueViewer from 'v-viewer'
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue'
import { devMode } from './mixin/devMode'
import 'viewerjs/dist/viewer.css'

Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);
Vue.use(VueViewer);
Vue.use(MyPlugin);

export default {
  mixins: process.env.NODE_ENV === 'development' ? [devMode] : [],
  components: {
    Header: () => import('./components/Header.vue'),
    SearchInput: () => import('./components/SearchInput.vue'),
    Textarea: () => import('./components/Textarea.vue'),
    ListItem: () => import('./components/ListItem.vue'),
    MessageItem: () => import('./components/MessageItem.vue'),
    OnlineStatus: () => import('./components/OnlineStatus.vue'),
    UploadFile: () => import('./components/UploadFile.vue'),
    MessageNotify: () => import('./components/Message/MessageNotify.vue'),
    Dropdown: () => import('./components/Dropdown.vue'),
    HoCS: () => import('./components/List/HoCS.vue'),
  },
  data() {
    return {
      isDev: process.env.NODE_ENV === 'development',
      isMobile: window.innerWidth <= 991 || false,
      isOpen: false,
      mobileViewList: true, // mobile 
      isFullScreen: false, // 
      heartbeat: { // 
        timer: null, // 
        sendTime: 5000, // 
        delayTime: 15000 // 
      },
      manualCloseConnect: false, // WS
      reConnectCount: 0, // 
      reConnectTime: 3000, // 
      offline: false, // 
      isOfflineForHeartbeat: false,
      WSReadState: 0, // ws
      wsObject: null,
      token: '',
      tokenAPIObj: null,
      userID: '',
      userName: '',

      /**  */
      // chatListIsInit: true, // lazy load chatList
      chatListLoadingFinish: false, // chatListlazy load
      chatList: [],
      chatListPage: 1,
      filterInput: '',
      filterSelect: true,
      infiniteId: +new Date(),

      /**  */
      msgIsInit: true, // lazy load msg
      msgLoadingFinish: false, // msglazy load
      msgTimer: null,
      prevPosY: 10000,
      messageList: [],
      messagePage: 1,
      input: '',
      targetPerson: {
        conversationID: '',
        userName: '',
        isOnline: false,
        stickerUrl: '',
      },
      leaveEnd: false, // 
    }
  },
  watch: {
    WSReadState(state) { // WS
      if(state !== 1) this.wsObject = null;
    },
    isOpen(status) {
      window.dispatchEvent(new CustomEvent('isOpen', { detail: { isOpen: status } }));
    }
  },
  computed: {
    isLogin() { // Web Socket
      return this.wsObject && this.WSReadState === 1;
    },
    onFilter() {
      return this.filterInput || !this.filterSelect;
    },
    filterChatList() { // 
      if(!this.isLogin) return [];
      const { chatList, filterSelect, filterInput } = this;
      let arr = chatList;

      if(!filterSelect)
        arr = arr.filter(el => !el.isRead);
      if(filterInput)
        arr = arr.filter(el => el.userName.includes(filterInput));
      console.log(' arr', arr)
      return arr;
    },
    stopReConnect() { // 
      return this.manualCloseConnect || this.reConnectCount > 5;
    },
    msgWrapper() {
      return this.$refs['msgWrapper'];
    },
    msgBody() {
      return this.$refs['msgBody'];
    },
  },
  methods: {
    /** WS start **/
    initProcess() { // _
      const { getSocketToken, initWs, offline, $_errorHandler } = this;
      return getSocketToken().then(() => initWs())
      .catch(err => {
        if(!offline) $_errorHandler(104, JSON.stringify(err));
        return false;
      });
    },
    getSocketToken() { // _Web Socket token
      const vm = this;
      const { $axios, tokenAPIObj } = vm;
      console.log('tokenAPIObj', tokenAPIObj)
      if(!tokenAPIObj) return Promise.reject();

      const _fetchConfig = {
        method: tokenAPIObj.method,
        headers: { "Content-Type": 'application/json' },
      };
      // tokenUrl
      const apiUrl = tokenAPIObj.url.includes('http')
        ? tokenAPIObj.url
        : window.location.origin + tokenAPIObj.url;
      console.log('tokenUrl', apiUrl)

      if(tokenAPIObj.method === 'POST') {
        Object.assign(_fetchConfig, { body: JSON.stringify(tokenAPIObj.data) });
      }
      return $axios(apiUrl, _fetchConfig)
        .then(res => {
          if(res.ticketVerified) {
            vm.token = res.token;
            return Promise.resolve();
          } else {
            return Promise.reject(res);
          }
        })
        .catch(err => {
          if(err.code === 'ERR_NETWORK') this.offline = true;
          return Promise.reject(err);
        });
    },
    initWs() { // _Web Socket
      const vm = this;
      const { token, getChatList, toggleModal, handleMessage } = vm;
      vm.wsObject = new WebSocket(`ws://192.168.100.242:5566/ws?token=${token}`)
      vm.wsObject.onopen = () => {
        if(vm.isOfflineForHeartbeat) console.log("oh my god! you're back! i'm gonna kill you again!!")
        vm.WSReadState = vm.wsObject.readyState;
        console.log(' on open')
        vm.reConnectCount = 0; // 
        vm.manualCloseConnect = false; // 
        vm.offline = false; // 
        vm.isOfflineForHeartbeat = false;
        vm.wsObject.send(JSON.stringify({ eventType: 95 })); // 
        getChatList();
        if(!vm.isOpen) toggleModal();
      } 
      vm.wsObject.onmessage = event => { // 0: 1: 2: 3 
        if(vm.wsObject.readyState === 1) handleMessage(JSON.parse(event.data));
      }
      vm.wsObject.onerror = () => {
        vm.WSReadState = 3;
        console.log(' onerror')
      }
      vm.wsObject.onclose = () => {
        vm.WSReadState = 3;
        vm.reConnectCount++;
        vm.clearTargetPerson();
        console.log('close connection')
        console.log('', vm.reConnectCount)
        if(!vm.stopReConnect) // 
          setTimeout(() => vm.initProcess(), vm.reConnectTime);
      }
      return !!vm.wsObject;
    },
    closeConnect() { // _Web Socket
      return new Promise(resolve => {
        this.manualCloseConnect = true;
        this.clearTargetPerson();
        if(this.isLogin) this.wsObject.close();
        resolve();
      })
    },
    locationReload() { // _
      window.location.reload();
    },
    checkInitData(data) { // _initWS
      const { $_errorHandler } = this;

      let isPass = '';
      if(!data || (data.method === 'POST' && !data.data)) {
        isPass = $_errorHandler(100);
      } else if(!data.userID) {
        isPass = $_errorHandler(101);
      } else if(!data.url) {
        isPass = $_errorHandler(102);
      } else if(!data.method) {
        isPass = $_errorHandler(103);
      }
      return !isPass;
    },
    /** WS  end  **/

    /**  start **/
    handleMessage(msgData) { // _
      const { data, eventType } = msgData;
      
      const resetTimer = () => {
        clearTimeout(this.heartbeat.timer);
        this.heartbeat.timer = null;
        this.heartbeat.timer = setTimeout(() => overMyDeadBody(), this.heartbeat.delayTime);
        console.log('reset heartbeat timer', this.heartbeat.timer)
      }
      const overMyDeadBody = () => { // 
        console.log('ha ha you dead professor KO')
        this.offline = true;
        this.isOfflineForHeartbeat = true;
        this.clearTargetPerson();
        if(this.isLogin) this.wsObject.close();
      }

      switch (eventType) {
        /**  */
        case 0: // 
        case 1: // 
          console.log('/', data);
          this.changeUserStatus(data, eventType)
          break;
        case 2: // 
          console.log('', msgData);
          this.receiveMsg(data);
          break;
        case 3: // 
          // TODO 
          break;
        case 5: //  &  heartbeat
          resetTimer();
          setTimeout(() => {
            this.wsObject.send(JSON.stringify({ eventType: 95 }));
          }, this.heartbeat.sendTime);
          break;
        case 7: // 
          console.log('', data);
          if(data.conversationID === this.targetPerson.conversationID) {
            this.messageList.forEach(el => el.isRead = true);
          }
          break;
        /**  */
        case 92: // 
          console.log('', msgData);
          this.wsObject.send(JSON.stringify(msgData));
          this.input = '';
          this.$refs.textArea.setEmptyHeight();
          break;
        case 97: // 
          console.log('', data)
          this.wsObject.send(JSON.stringify(msgData));
          break;
        default:
          // unknown message
          console.log('unknown message!', msgData);
          break;
      }
    },
    sendFile(file) { // _
      const { $axios, token, targetPerson } = this;
      const conversationID = targetPerson.conversationID;
      const formData = new FormData();

      formData.append("file", file, file.name);
      return $axios(`Websocket/UpLoadFile?conversationID=${conversationID}`,{
        method: 'POST',
        headers: { 'Authorization': `Bearer ${token}` },
        body: formData
      }).then(res => { console.log('res for upload file', res) });
    },
    sendMsg() { // _
      const conversationID = this.targetPerson.conversationID;
      const message = this.input;

      if(!message) return;
      console.log('', { eventType: 92, data: { conversationID, message } })
      this.handleMessage({ eventType: 92, data: { conversationID, message } })
    },
    receiveMsg({ // _
        messageType, conversationID, conversationName,
        senderID, senderName, message, senderTime, stickerUrl
      }) { // _
      const targetIndex = this.chatList.findIndex(el => el.conversationID === conversationID);
      const isTalkNow = conversationID === this.targetPerson.conversationID; // 
      const isMyMessage = senderID === this.userID; // 
      const getLastMsg = () => { // 
        let type = messageType === 2 ? ''
          : messageType === 3 ? ''
          : '';
        return message.text
          ? message.text
          : isMyMessage ? `${type}` : `${type}`; 
      }

      // 
      if(targetIndex !== -1) {
        const unshiftData = this.chatList[targetIndex];

        // 
        if(!isTalkNow) unshiftData.isRead = false;
        unshiftData.lastMessage = getLastMsg();
        this.chatList.splice(targetIndex, 1);
        this.chatList.unshift(unshiftData);
      } else { // 
        this.chatList.unshift(
          {
            lastMessage: getLastMsg(),
            senderID,
            conversationID,
            isRead: false,
            isOnline: true,
            userID: senderID,
            userName: senderName,
            conversationName,
            senderTime,
            stickerUrl,
          }
        )
      }
      // 
      if(isMyMessage) {
        const DOM = document.querySelector('.listItem');
        if(DOM) DOM.scrollIntoView();
      }
        
      // 
      if(isTalkNow) {
        this.messageList.push({
          message,
          conversationID,
          isRead: false,
          messageID: "",
          messageType,
          senderID,
          senderTime: new Date(),
        });
        // 
        this.handleMessage({ eventType: 97, data: { conversationID } });
        //  
        !this.leaveEnd || isMyMessage
          ? this.goEnd()
          : this.$refs.useMsgNotify.active();
      }
    },
    changeUserStatus(data, eventType) { // _
      this.chatList.map(el => { // 0:  1:
        if(el.userID === data.userID) el.isOnline = !eventType;
      });
    },
    /**   end  **/

    /**  start **/
    getChatList() { // _
      const vm = this;
      const { $axios, token, chatListPage, userID, getChatList } = vm;
      $axios(`Conversation/GetRecentConversation?PageIndex=${chatListPage}&PageSize=20`,{
        method: 'GET',
        headers: { 'Authorization': `Bearer ${token}` }
      }).then(res => {
        const items = res.data.items;
        if(items.length) {
          vm.chatListPage++;
          items.forEach(el => {
            el.isRead = el.senderID === userID || el.isRead;
          });
          vm.chatList.push(...items);
          getChatList();
          console.log('', vm.chatList)
        }

        if(items.length <= 19) {
          vm.chatListLoadingFinish = true;
          console.log('')
        }
        // 20230925 
        // if(vm.chatListIsInit)
        //   setTimeout(() => vm.chatListIsInit = false, 50)
        // if(vm.onFilter && !vm.chatListLoadingFinish) vm.chatListLazyLoad(true)
      });
    },
    // chatListLazyLoad(isVisible) { // 20230925  - _lazyLoad
    //   const { isLogin, chatListIsInit, getChatList } = this;
    //   if(!isLogin || !isVisible) return
    //   if(!chatListIsInit) getChatList();
    // },
    addNewConnect({ targetID }) { // _ID
      const { $axios, token } = this;
      const connectPerson = () => {
        $axios(`Conversation/GetConversationChat?targetID=${targetID || 'test2488'}&PageIndex=1&PageSize=20`,{
          method: 'GET',
          headers: { 'Authorization': `Bearer ${token}` }
        }).then(({ data }) => {
          const { conversation, user } = data;
          const { conversationID, conversationName } = conversation;
          const { userID, userName, lastOnlineTime, isOnline, stickerUrl } = user;
          const IsExists = this.chatList.findIndex(el => el.conversationID === conversationID) > -1;

          // 
          IsExists
            ? document.getElementById(conversationID).scrollIntoView()
            : this.chatList.unshift({
              conversationID, conversationName,
              userID, userName,
              lastMessage: "", senderTime: new Date(),
              lastOnlineTime, isOnline, stickerUrl
            });

          // 
          this.connect({
            conversationID, userID, userName,
            stickerUrl: stickerUrl
              ? `background-image: url(${stickerUrl});`
              : 'background-color: #212529'
          });
        });
      }
      if(this.isLogin) {
        connectPerson();
      } else if(this.checkInitData(this.tokenAPIObj)) {
        console.log('', this.userID, this.tokenAPIObj)
        this.initProcess().then(res => {
          if(res) connectPerson();
        });
      }
    },
    connect(data) { // _
      console.log(`connect ${data.userName} `, data)
      const vm = this;

      if(!vm.isOpen) vm.toggleModal();
      vm.mobileViewList = false;
      vm.messagePage = 1;
      vm.messageList = [];
      vm.msgIsInit = true;
      vm.msgLoadingFinish = false;
      Object.assign(vm.targetPerson, data)
      vm.getMessageList().then(() => {
        vm.msgIsInit = false;
        vm.handleMessage({ // 
          eventType: 97,
          data: { conversationID: data.conversationID }
        });
        setTimeout(() => vm.goEnd(), 0);
      })
      vm.chatList.map(el => { // 
        if(el.conversationID === data.conversationID) el.isRead = true;
      })
    },
    msgLazyLoad(isVisible) { // _lazyLoad
      const { isLogin, msgIsInit, $debounce, getMessageList } = this;
      if(!isLogin || !isVisible) return
      if(!msgIsInit) $debounce(getMessageList, this.msgTimer); // 
    },
    getMessageList() { // _
      const vm = this;
      const { $axios, messagePage, token, targetPerson } = vm;
      const { conversationID } = targetPerson;
      return $axios(`Message/GetByConversationID?PageIndex=${messagePage}&PageSize=20&conversationID=${conversationID}`,{
        method: 'GET',
        headers: { 'Authorization': `Bearer ${token}` }
      }).then(res => {
        const items = res.data.items;
        if(items.length) {
          vm.messageList.unshift(...items);
          vm.messagePage++;
          vm.goEnd(true);
          console.log('', vm.messageList)
        }
        if(items.length <= 19) {
          vm.msgLoadingFinish = true;
          console.log('')
        }
        return
      });
    },
    scrollEvent({ target }) {
      const { scrollHeight, scrollTop, clientHeight } = target;
      this.leaveEnd = scrollHeight - (scrollTop + clientHeight) > 300;
    },
    goEnd(keepNoJump) { // _
      const msgWrapper = this.msgWrapper;
      const msgBody = this.msgBody;
      this.$nextTick(() => {
        msgWrapper.scrollTop = keepNoJump ? msgBody.scrollHeight - this.prevPosY : msgBody.scrollHeight;
        this.prevPosY = msgBody.scrollHeight;
      })
    },
    hideModal(close) { // _
      close ? this.isOpen = false : this.mobileViewList = true;
      this.clearTargetPerson();
    },
    clearTargetPerson() {
      this.targetPerson = {
        conversationID: '',
        userName: '',
        isOnline: false,
        stickerUrl: '',
      };
    },
    /**   end  **/


    /**  start **/
    toggleModal() { // _modal
      this.isOpen = !this.isOpen;
      if(!this.isLogin) this.$_errorHandler(200);
    },
    _setListener(setType) { // _Listener
      const windowListenerList = [
        'toggleModal', //  web socket
        'setData', //  web socket 
        'openChatService', //  web socket 
        'addNewConnect', // 
        'closeConnect', //  web socket
        'shutdown', //  web socketlistener
        'offline', // 
        'online', // 
      ];
      const METHOD = setType === 'add' ? 'addEventListener' : 'removeEventListener';
      windowListenerList.forEach(funcName => {
        window[METHOD](funcName, this._listenerFunc)
      })
    },
    _listenerFunc(e) { // _function
      switch (e.type) {
        case 'setData':
        case 'openChatService':
          if(e.type === 'openChatService') {
            console.log('WS_openChatService')
            if(!this.isLogin) {
              if(this.tokenAPIObj) {
                console.log('WS_already setData! initProcess', this.tokenAPIObj)
                this.initProcess();
              } else {
                if(this.checkInitData(e.detail)) {
                  this.userID = e.detail.userID;
                  this.isFullScreen = !!e.detail.isFullScreen;
                  this.tokenAPIObj = e.detail;
                  console.log('WS_initProcess', this.userID, this.tokenAPIObj)
                  this.initProcess();
                }
              }
            } else {
              console.log('WS_already connect! toggleModal')
              // 
              e.detail?.isFullScreen ? this.isOpen = true : this.toggleModal();
            }
          } else {
            console.log('setData', e.detail || undefined)
            if(this.checkInitData(e.detail)) {
              this.userID = e.detail.userID;
              this.isFullScreen = !!e.detail.isFullScreen;
              this.tokenAPIObj = e.detail;
            }
          }
          break;
        case 'toggleModal':
          console.log('WS_toggleModal')
          this.toggleModal();
          break;
        case 'addNewConnect':
          console.log('WS_addNewConnect', e.detail)
          this.addNewConnect(e.detail);
          break;
        case 'closeConnect':
          console.log('WS_closeConnect', e)
          this.closeConnect();
          break;
        case 'shutdown':
          console.log('WS_shutdown', e)
          this.closeConnect().then(() => this._setListener('remove'))
          break;
        case 'offline':
        case 'online':
          console.log('page is', e.type)
          break;
        default:
          break;
      }
    },
    _passData() { // OK () _datamethods
      const { $data, toggleModal } = this;
      const event = new CustomEvent('init', {
        detail: {
          data: { $data },
          methods: { toggleModal }
        }
      });
      window.dispatchEvent(event);
    },
    _windowResize(e) { // _resize
      this.isMobile = e.target?.innerWidth <= 991 || false;
    },
    _setVh() { // _ios
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty("--vh", `${vh}px`);
      window.onresize = () => {
        let vh = window.innerHeight * 0.01;
        document.documentElement.style.setProperty("--vh", `${vh}px`);
      };
    }
    /**   end  **/
  },
  created() {
    this._passData();
    this._setVh();
    window.addEventListener("resize", this._windowResize);
    this._setListener('add');
  },
  destroyed() {
    window.removeEventListener("resize", this._windowResize);
  },
};
</script>
<style lang="scss">
@import './assets/scss/main.scss';
</style>